/**
 * Layout
 */

html, body {
  background-color: #0F101A;
  color: $perano;
}

img {
  max-width: 100%;
}

.section-1 {
  background-image: linear-gradient(to bottom, #1a1c2d , #0F101A);

  &__header {
    align-items: center;
    display: flex;
    justify-content: space-between;
    padding-top: 5rem;
  
    nav {
      ul {
        display: flex;
        list-style-type: none;
        margin: 0;
        padding: 0;
      }
  
      li {
        &:last-child {
          a {
            padding-right: 0;
          }
        }
      }
  
      a {
        color: $perano;
        display: inline-block;
        font-weight: bold;
        padding: 0rem 2rem 1rem;
  
        &:hover, &:focus, &:active {
          color: #FFD128;
          text-decoration: none;
        }

        @include max(375px) {
          padding: 0rem 1.5rem 1rem;
        }
      }
    }

    @include max(768px) {
      flex-direction: column;
      padding-top: 4rem;

      .logo {
        margin-bottom: 3.5rem;
      }
    }
  }

  &__landing-info {
    display: flex;
    margin: 2.5rem 0 6rem;

    @include max(768px) {
      flex-direction: column;
      align-items: center;
    }

    & > div {
      align-items: center;
      display: flex;
      justify-content: center;
      width: 50%;

      @include max(768px) {
        width: 100%;
      }
    }

    &__texts {
      & > div {
        max-width: 43rem;

        @include max(768px) {
          margin: 3rem 0 8rem;
          text-align: center;
        }
      }

      @include max(768px) {
        a {
          margin: 0 auto;
        }
      }
  
      h1 {
        color: #fff;
        font-size: 4rem;
        margin: 0 0 2.5rem;

        @include min(1024px) {
          font-size: 5rem;
          }

        @include max(600px) {
          font-size: 3rem;
        }
      }
  
      h3 {
        color: $perano;
        font-weight: normal;
        line-height: 2.5rem;
        margin: 0 0 3.5rem;

        @include small-breakpoint {
          line-height: 3rem;
        }
      }
    }

    &__image {
      padding: 3rem 0;
      position: relative;

      @include max(768px) {
        max-width: 40rem;
      }

      .phone {
        z-index: 5;

        @include max(425px) {
          img {
            max-width: 20rem;
          }
        }
      }

      .phone-bg {
        border-radius: 2rem;
        height: 100%;
        right: 0;
        top: 0;
        overflow: hidden;
        position: absolute;
        width: 50%;
        z-index: 1;

        img {
          margin: -3rem 0 0 -3rem;
          max-width: inherit;
        }

        @include max(1024px) {
          width: 65%;
        }
      }
    }
  }

}

.section-2 {
  padding-top: 8rem;
  position: relative;

  &__bg-absolute {
    background-image: url(./../assets/wave-path.svg);
    background-position: top center;
    background-repeat: no-repeat;
    height: 100vh;
    min-height: 90.2rem;
    position: absolute;
    top: -15rem;
    width: 100%;
    z-index: 1;
  }

  .container {
    position: relative;
    z-index: 10;
  }

  &__about {
    max-width: 45rem;

    @include max(768px) {
      margin-bottom: 5rem;
    }

    p {
      margin-bottom: 3rem;
    }
  }

  &__steps {
    max-width: 37.5rem;
    margin: 0 auto;

    @include max(768px) {
      max-width: 45rem;
    }
  }

  .pomodoro-steps {
    list-style-type: none;
    margin: 0;
    padding: 0;

    li {
      align-items: center;
      display: flex;
      margin-bottom: 3rem;

      img {
        margin-right: 2rem;
        max-width: 6rem;
      }

      p {
        margin: 0;
      }
    }
  }
  
  @include max(768px) {
    .flex-row {
      align-items: center;
      flex-direction: column;
    }
  }
}

.section-3 {
  padding-top: 8rem;
  position: relative;
  z-index: 2;

  .flex-row {

    @include max(768px) {
      align-items: center;
      flex-direction: column;
    }

    & > div {
      text-align: center;

      @include max(768px) {
        margin-bottom: 7rem;
      }
    }

    h3 {
      color: $perano;
    }

    p {
      margin: 0 auto;
      max-width: 30rem;
    }
  }
}

.section-4 {
  padding: 15rem 0 22rem;

  @include max(768px) {
    padding: 5rem 0 22rem;
  }

  .cta {
    display: flex;
    align-items: center;
    justify-content: center;

    @include max(768px) {
      flex-direction: column;
      text-align: center;
    }
  

    h2 {
      color: #fff;
      display: inline-block;
      font-size: 3rem;
      margin: 0;
    }
  
    a {
      margin-left: 3rem;

      @include max(768px) {
        margin: 3rem 0 0;
      }
    }
  }
  
}

#footer {
  background-image: linear-gradient(to bottom, #171926 , #0F101A);

  .sunset-icon {
    text-align: center;

    img {
      display: inline-block;
      margin-top: -116px;
    }
  }

  .flex-row {
    padding: 4rem 0;

    @include max(768px) {
      align-items: center;
      flex-direction: column;
      text-align: center;
    }

    p {
      color: #575A85;
      font-size: 1.4rem;
      margin: 0;
    }

    a {
      color: #575A85;
      display: inline-block;
      padding-left: 2rem;
    }

    .flex-small {
      display: flex;
      align-items: center;

      &:last-child {
        justify-content: flex-end;

        @include max(768px) {
          margin: 2rem 0 0;
        }
      }
    }
  }
}